.container {
  max-width: 101% !important;
  margin-left: 0;
  margin-right: 0;
  padding: 0;
}

.hero {
  background: url(/static/media/food_v.b719d5d9.svg);
  height: 800px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1,
.hero h2 {
  color: rgb(180, 25, 25);
}

.hero h2 {
  line-height: 0.2;
  margin-top: 1%;
}

.hero h1 {
  font-size: 3.9em;
  color: rgb(6, 14, 15);
  padding-top: 23%;
  margin-top: 1%;
}

/* Extra small devices (portrait phones, less than 576px)*/
/*  No media query for `xs` since this is the default in Bootstrap */

/* Small devices (landscape phones, 576px and up)*/
@media (min-width: 357px) {
  .hero {
    height: 500px;
  }
  .hero h1 {
    font-size: 2.1em;
    color: rgb(6, 14, 15);
    padding-top: 26%;
  }
}

@media (min-width: 576px) {
  /* Medium devices (tablets, 768px and up) */
}
@media (min-width: 768px) {
  .hero h1 {
    font-size: 2.9em;
    color: rgb(6, 14, 15);
    padding-top: 20%;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .hero {
    background: url(/static/media/food_h.2131e10b.svg);
    height: 390px;
    background-size: cover;
    background-position: center;
    position: relative;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .hero h1 {
    font-size: 2.9em;
    color: rgb(6, 14, 15);
    padding-top: 8%;
  }
  .hero h2 {
    margin-top: -3%;
  }
}

.card {
  box-shadow: none;
  border: none !important;
  border-radius: none !important;
}
.col-md-12 {
  margin-top: -26px;
}

.card-header {
  background-color: transparent;
  border-bottom: none;
}

.form-control {
  margin-top: 12%;
}

.list-group-item p {
  font-size: 16px;
}

h2 {
  color: white;
}
.recipetitle {
  color: black;
  margin-top: 1%;
  margin-bottom: 1%;
}

.btn-primary {
  color: #fff;
  background-color: rgb(43, 63, 33) !important;
  border: none;
  width: 6.5em;
}
.btn-primary:hover {
  background-color: rgb(122, 150, 109) !important;
}
.howto {
  margin-top: 1%;
}

.youtube {
  padding: 0.38em 30px;
  color: white;
  background-color: rgb(168, 12, 12);
  text-decoration: none;
  border: none;
  border-radius: 2;
}

.youtube:hover {
  color: white;
  background-color: rgb(160, 80, 80);
}

@media (max-width: 1800px) {
}

@media (max-width: 767px) {
  .howto {
    margin-top: 6%;
  }
  .btn-container {
    float: left;
    margin-bottom: 15px;
    color: #fff;
    background-color: rgb(43, 63, 33);
    border: none;
  }
}

.savedrecipes {
  margin-left: 3.9%;
  margin-top: 5%;
  font-size: 2em;
}
.card-body {
  background-color: #30352b;
  /* padding: 1% 0; */
}

input {
  margin-top: 11%;
}

btn .extra {
  display: none;
}

.jumbotron {
  padding: 100px;
}

.App {
  font-family: sans-serif;
  width: 100%;
  margin: 0 auto;
  padding: 15%;
}

h1 {
  size: 1em;
  color: white;
  text-align: center;
  margin-top: -8%;
  margin-bottom: 5%;
}

form {
  max-width: 300px;
  width: 100%;
  margin: 0 auto;
}

label,
input {
  display: block;
  min-width: 290px;
  width: 100%;
}

label {
  margin-bottom: 5px;
  height: 22px;
  color: white;
}

input {
  margin-top: 5%;
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid #777;
}

input.error {
  border-color: red;
}

.input-feedback {
  color: rgb(235, 54, 54);
  margin-top: -15px;
  font-size: 14px;
  margin-bottom: 20px;
}

button {
  padding: 8px 19px;
  background-color: #dc3545;
  color: white;
  border: none;
}

button:hover {
  cursor: pointer;
  background-color: white;
  color: rgb(70, 153, 179);
}

.navbar {
  border-radius: 0;
  z-index: 9999;
  /* background-color: transparent; */
  background-color: #e7dbd3;
  box-shadow: none !important;
}
.logo {
  font-size: 1.9em;
  font-weight: 900;
  color: rgb(33, 44, 36);
}

.navbar-toggler {
  background: rgba(252, 252, 252, 0.59);
}

.mb-2 {
  margin-bottom: 0 !important;
}

